/* src/components/Footer.css */

.footer {
  width: 100%;
  padding: 2rem 1.5rem;
  background-color: #1e2d3d; /* Rich dark background for consistency */
  color: #ffffff;
  text-align: center;
  box-shadow: 0 -4px 6px rgba(0, 0, 0, 0.1);
  margin-top: 3rem;
}

.footer-content {
  max-width: 1200px;
  margin: 0 auto;
}

.footer-nav {
  display: flex;
  justify-content: center;
  flex-wrap: wrap; /* Allows wrapping on smaller screens */
  gap: 2rem;
  margin-top: 1.5rem;
}

.footer-nav a {
  color: #ffffff;
  text-decoration: none;
  font-weight: 500;
  font-size: 1rem;
  transition: color 0.3s ease, text-decoration 0.3s ease;
}

.footer-nav a:hover {
  color: #28a745; /* Green accent for footer links */
  text-decoration: underline; /* Underline for better emphasis */
}

/* Social Media Placeholder Styling */
.social-media {
  margin-top: 1rem;
  display: flex;
  justify-content: center;
  gap: 1.5rem;
}

.social-media a {
  color: #ffffff;
  font-size: 1.5rem;
  transition: color 0.3s ease, transform 0.3s ease;
}

.social-media a:hover {
  color: #28a745; /* Green color on hover */
  transform: scale(
    1.1
  ); /* Slightly larger size on hover for better visibility */
}

/* Responsive Footer Styling */
@media (max-width: 768px) {
  .footer-nav {
    gap: 1rem;
  }

  .footer-content {
    text-align: center;
  }
}

/* Styling for the image */
.banner-image {
  width: 90%; /* Make image take full width of its container */
  height: auto; /* Maintain aspect ratio */
  max-width: 500px; /* Limit maximum size to maintain readability */
  border-radius: 8px; /* Optional: rounded corners */
  object-fit: cover; /* Ensure the image fills its container nicely */
}
