/* ZipCodePage.css */

/* General Reset */
body,
html {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  background-color: #f8f8f8; /* Light background similar to the one in the image */
}

/* Container for the entire page content */
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 20px;
}

/* Header */
header {
  width: 100%;
}

/* Main content container with a background image */
.background-container {
  position: relative; /* Position relative to contain the overlay and card */
  width: 100%;
  max-width: 800px;
  padding: 40px;
  border-radius: 10px;
  background-image: url("./../../images/insurance1.jpeg"); /* Update path as needed */
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  margin-top: 40px;
}

/* Background overlay to add the fade effect */
.background-container::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(
    255,
    255,
    255,
    0.8
  ); /* White overlay to fade the background */
  border-radius: 10px; /* Ensure overlay matches container shape */
  z-index: 1; /* Place it above the background, but below the form content */
}

/* Zip code form container */
.zip-code-card {
  position: relative;
  z-index: 2; /* Place the form content above the background overlay */
  background: transparent; /* Make form background transparent so overlay shows */
  padding: 30px;
  text-align: center;
}

.zip-code-card h1 {
  font-size: 1.8rem;
  font-weight: bold;
  color: #333333;
  margin-bottom: 20px;
}

/* Input label */
.input-label {
  display: block;
  font-size: 1rem;
  font-weight: bold;
  margin-bottom: 10px;
  color: #555555;
}

/* Input container for ZIP code */
.input-container {
  margin-bottom: 20px;
}

/* Input styling for ZIP code */
.zip-input {
  width: 100%;
  padding: 15px;
  font-size: 1rem;
  border: 1px solid #cccccc;
  border-radius: 8px;
  outline: none;
  text-align: center;
}

/* Button styling */
.get-started-button {
  background-color: #d32f2f; /* Red color matching the "Start Now" button */
  color: white;
  font-size: 1.2rem;
  font-weight: bold;
  padding: 15px;
  border: none;
  border-radius: 8px;
  width: 100%;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
  position: relative;
}

.get-started-button:hover {
  background-color: #b71c1c; /* Slightly darker red on hover */
}

/* Adding right arrow icon */
.get-started-button::after {
  content: "➔";
  font-size: 1.2rem;
  position: absolute;
  right: 20px;
  top: 50%;
  transform: translateY(-50%);
}

/* Change button color when clicked */
.get-started-button:active {
  background-color: #9b1b1b; /* Even darker red to indicate clicking */
  transform: scale(0.95); /* Slight scaling effect to simulate button press */
}

/* Optional: Change the class-based effect from JavaScript */
.green-active {
  background-color: #b71c1c !important; /* Use a color that blends well with red theme */
  transform: scale(0.95); /* Similar to :active for consistency */
}

/* Error message styling */
.error-message {
  color: #d32f2f;
  font-size: 0.9rem;
  margin-top: 10px;
}

/* Footer */
footer {
  margin-top: 40px;
  width: 100%;
}
